.header{
    position: sticky;
    top: 0;
    min-height: 80px;
    width: 100;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
    background-color:   #4e6e42;
    /* border-radius: 15px; */
    z-index: 1000;
    /* opacity: calc(72%); */
}
.logo {
    text-decoration: none;
    text-transform: uppercase;
    color: white;
    padding-left: 40px;
    font-weight: 800;
  }
.header-links {
    display: flex;
}
.header-links ul li{
    list-style: none;
    display: inline-block;
    font-size: 18px;
}
.header-links ul li a{
    text-decoration: none;
    text-transform: uppercase;
    color: white;
    padding: 0 15px;
}
.header-links ul li a:hover{
    
   color: black;
}